body {

    font-family: Roboto, "Helvetica Neue", sans-serif;
    font-size: 15px;
    font-weight: 400;
    word-break: break-word;
    overflow-x: hidden;
    padding-bottom: 50px;

    @media(min-width: 575px) {
        padding-bottom: 0;
    }
}

img {
    max-width: 100%;
    height: auto;
}

.uppercase {
    text-transform: uppercase;
}

a {
    color: $green;
    text-decoration: none;

    &:hover {
        text-decoration: none;
        color: $blue;
    }
}


.comment-notes {
    display: none;
}