.btn {
    border: 1px solid $text_gray;
    border-radius: 0;
    font-weight: bold;
    background-color: $text_gray;
    color: #fff;
}

.btn-dark {
    background-color: $blue;
}

.btn-blue {
    background-color: $blue_mint;
    border-color: $blue_mint;
    color: #fff;

    &:hover {
        background-color: $blue;
        color: #fff;
        border-color: $blue;
    }
}

.btn-loadmore {
    border-radius: 40px;
    padding-left: 30px;
    padding-right: 30px;
}